
/*======================================================*/
/* FONTS */
/*======================================================*/
@font-face {
  font-family: "circe-regular";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/Circe-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "circe-bold";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/Circe-Bold.ttf") format("truetype");
}

@font-face {
  font-family: "circe-extra-bold";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/Circe-ExtraBold.ttf") format("truetype");
}

@font-face {
  font-family: "circe-light";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/Circe-Light.ttf") format("truetype");
}

/*======================================================*/
/* NORMALIZE AND RESET */
/*======================================================*/
/*! normalize-scss | MIT/GPLv2 License | bit.ly/normalize-scss */
html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
footer,
header,
nav,
section {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

figcaption,
figure {
  display: block;
}

figure {
  margin: 1em 40px;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

main {
  display: block;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: inherit;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

audio,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

input {
  overflow: visible;
}

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type="search"]::-webkit-search-cancel-button, [type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  display: table;
  max-width: 100%;
  padding: 0;
  color: inherit;
  white-space: normal;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

details {
  display: block;
}

summary {
  display: list-item;
}

menu {
  display: block;
}

canvas {
  display: inline-block;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

/* reset */
*, *::after, *::before {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
}

body, html {
  height: 100%;
  width: 100%;
}

form {
  margin: 0;
  padding: 0;
}

input, input:focus, textarea, select {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
  -webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
  outline: none !important;
}

button,
textarea,
.select select,
input[type="text"],
input[type="email"],
input[type="search"],
input[type="submit"],
input[type="button"],
input[type="reset"],
input[type="number"],
input[type="range"] {
  appearance: none;
  border-radius: 0;
}

select[disabled] {
  cursor: default;
}

select::-ms-expand {
  display: none;
}

input {
  vertical-align: middle;
}

input::-ms-clear {
  display: none;
}

input[type="radio"]:focus {
  -moz-outline-radius: 12px !important;
  -moz-outline-offset: 0 !important;
}

input[type="checkbox"]:focus {
  -moz-outline-offset: -1px !important;
  -moz-outline: 1px solid #000 !important;
}

input[type="radio"],
input[type="checkbox"],
select {
  cursor: pointer;
}

blockquote, q {
  quotes: none;
}

blockquote::before, blockquote::after, q::before, q::after {
  content: none;
}

ins {
  text-decoration: none;
}

del {
  text-decoration: line-through;
}

table {
  width: 100%;
}

/* reset end */
/* browserupgrade */
.old-ie {
  overflow: hidden;
}

.old-ie body {
  overflow: hidden;
}

.browserupgrade {
  position: fixed;
  height: 100%;
  width: 100%;
  padding: 0.2em 0;
  margin: 0;
  font-size: 28px;
  line-height: 42px;
  color: #000;
  background: #ccc;
  z-index: 99999;
}

.browserupgrade p {
  position: absolute;
  width: 100%;
  top: 50%;
  margin: -22px 0 0;
  padding: 0;
  text-align: center;
  vertical-align: middle;
}

/* browserupgrade end */
/* no js warning */
.no-js-warning {
  border: 2px solid #f00;
  color: #f00;
  background: #fff;
  padding: 5px 20px;
  font-size: 18px;
}

/* no js warning end */
.gray-box {
  background: white;
}

/*======================================================*/
/* TYPOGRAPHY */
/*======================================================*/
body {
  position: relative;
  font-family: "circe-regular", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: 1.6rem;
  line-height: 1.5;
  color: #262626;
  min-width: 1024px;
  background: #f5f5f5 url(../img/empty.png) no-repeat left top fixed;
}

body *::selection {
  background-color: #6e52dd;
  color: white;
}

h1, h2, h3, h4, h5, h6 {
  padding: 0;
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  color: black;
  margin: 0 0 2.4rem;
}

@media (max-width: 639px) {
  h1, h2, h3, h4, h5, h6 {
    margin-bottom: 2rem;
  }
}

h1:last-child, h2:last-child, h3:last-child, h4:last-child, h5:last-child, h6:last-child {
  margin-bottom: 0;
}

h1 a, h2 a, h3 a, h4 a, h5 a, h6 a {
  text-decoration: none;
  color: inherit;
}

.no-touchevents h1 a:hover, .no-touchevents h2 a:hover, .no-touchevents h3 a:hover, .no-touchevents h4 a:hover, .no-touchevents h5 a:hover, .no-touchevents h6 a:hover {
  color: #6e52dd;
  text-decoration: underline;
}

h1 {
  font-size: 4.8rem;
  line-height: 1.25;
}

@media (max-width: 991px) {
  h1 {
    font-size: 3.6rem;
    line-height: 1.33;
  }
}

@media (max-width: 639px) {
  h1 {
    font-size: 3rem;
    line-height: 1.4;
  }
}

h2 {
  font-size: 3.6rem;
  line-height: 1.33;
}

@media (max-width: 991px) {
  h2 {
    font-size: 3rem;
    line-height: 1.36;
  }
}

@media (max-width: 639px) {
  h2 {
    font-size: 2.6rem;
    line-height: 1.41;
  }
}

h3 {
  font-size: 2.4rem;
  line-height: 1.5;
}

@media (max-width: 639px) {
  h3 {
    font-size: 2.2rem;
    line-height: 1.47;
  }
}

h4 {
  font-size: 1.8rem;
  line-height: 1.67;
}

h5 {
  font-size: 1.6rem;
  line-height: 1.5;
}

h6 {
  font-size: 1.2rem;
  line-height: 1.88;
  text-transform: uppercase;
}

p {
  padding: 0;
  margin: 0 0 2.4rem;
}

hr {
  line-height: 1px;
  height: 0;
  border: none;
  background: none;
  border-bottom: 1px solid lightgray;
  margin-bottom: 2.4rem;
}

a {
  color: blue;
  text-decoration: underline;
  cursor: default;
}

a:link {
  cursor: pointer;
}

a:hover {
  text-decoration: none;
}

a[href^="javascript:"] {
  cursor: default;
}

img {
  border: none;
  vertical-align: top;
  max-width: 100%;
}

table img {
  max-width: none;
}

b, strong, th {
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
}

i, em {
  font-style: italic;
}

i b, i strong, em b, em strong, b i, b em, strong i, strong em {
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-style: italic;
}

.entry-text {
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.2em;
  margin-bottom: 2.4rem;
}

.entry-text b, .entry-text strong {
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
}

.entry-text i, .entry-text em {
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-style: italic;
}

.entry-img {
  position: relative;
  overflow: hidden;
  height: 0;
  padding-top: 60%;
  margin-bottom: 2.4rem;
}

@media (max-width: 479px) {
  .entry-img {
    height: 0;
    padding-top: 56.25%;
  }
}

.entry-img img {
  position: absolute;
  left: 50%;
  top: 50%;
  max-width: 100%;
  max-height: none;
  min-width: 0;
  min-height: 0;
  text-align: left;
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  transform: translate(-50%, -50%);
  object-fit: cover;
  font-family: "object-fit: cover";
}

.entry-img__title {
  position: absolute;
  top: 50%;
  left: 50%;
  padding-right: 0 !important;
  padding-left: 0 !important;
  margin: 0;
  transform: translate(-50%, -50%);
  z-index: 10;
  font-size: 4.8rem;
  line-height: 1.25;
  color: #f5f5f5;
  text-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  text-align: center;
}

@media (max-width: 1279px) {
  .entry-img__title {
    font-size: 3.6rem;
    line-height: 1.33;
  }
}

@media (max-width: 639px) {
  .entry-img__title {
    font-size: 3rem;
    line-height: 1.4;
  }
}

@media (max-width: 479px) {
  .entry-img__title {
    font-size: 22px;
    line-height: 1;
  }
}

.hide {
  display: none !important;
}

.mark {
  color: tomato;
  font-style: inherit;
  font-weight: inherit;
}

.text-color-alt {
  color: #6e52dd;
}

.align-center {
  text-align: center;
}

.align-left {
  text-align: left;
}

.align-right {
  text-align: right;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.nobr {
  white-space: nowrap;
}

.hide-text {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.hide-text:active, .hide-text:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}

/*content img */
.layout-article > img, .layout-article .imgc {
  text-align: center;
  display: block;
  margin: 0 auto 2.4rem;
  width: 1000px;
  max-width: 100%;
  clear: both;
}

.layout-article .imgl {
  text-align: left;
  float: left;
  margin: 0 2.4rem 2.4rem 0;
}

.layout-article .imgr {
  text-align: right;
  float: right;
  margin: 0 0 2.4rem 2.4rem;
}

.layout-article .imgl, .layout-article .imgr {
  max-width: 40%;
}

@media (max-width: 767px) {
  .layout-article .imgl, .layout-article .imgr {
    float: none;
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
  }
}

.imgc figcaption, .imgl figcaption, .imgr figcaption {
  display: block;
  padding: 2.4rem 0 0;
  margin: 0;
  font-size: 1.4rem;
  line-height: 1.71;
}

/*content img end */
/*columnize */
.columnize, .columnize--3, .columnize--4 {
  column-count: 2;
  column-gap: 2em;
  margin-bottom: 2.4rem;
  font-size: 0.9em;
}

.columnize--3 {
  column-count: 3;
  font-size: 0.85em;
}

.columnize--4 {
  column-count: 4;
  font-size: 0.8em;
}

@media (max-width: 1199px) {
  .columnize--4 {
    column-count: 3;
  }
}

@media (max-width: 991px) {
  .columnize--3, .columnize--4 {
    column-count: 2;
  }
}

@media (max-width: 767px) {
  .columnize, .columnize--3, .columnize--4 {
    column-count: 1;
  }
}

/*columnize end */
/*columns */
.columns::after {
  content: '';
  display: table;
  clear: both;
}

.column {
  float: left;
  width: calc(50% - 1em);
  margin: 0 1em;
}

@media (max-width: 639px) {
  .column {
    float: none;
    width: auto;
    margin: 0 0 5rem;
  }
  .column:last-child {
    margin-bottom: 0;
  }
}

.column:nth-child(odd) {
  margin-left: 0;
}

.column:nth-child(even) {
  margin-right: 0;
}

