/*list end */
/*table */
table {
  border-collapse: collapse;
  border-spacing: 0;
  width: auto;
  margin-bottom: 2.4rem;
}

table td, table th {
  font-size: 1.6rem;
  line-height: 1.5;
  color: inherit;
  padding: 8px 15px;
  border-color: #262626;
}

.table-auto {
  position: relative;
  overflow: auto;
  max-width: 100%;
  -webkit-overflow-scrolling: touch;
  margin-bottom: 2.4rem;
}

.table-auto table {
  margin-bottom: 0;
  width: auto;
  min-width: 100%;
}

.tbl {
  border: none;
}

.tbl td, .tbl th {
  color: #262626;
  background-color: #f5f5f5;
  border: 0 solid #6e52dd;
  font-size: 1.4rem;
  line-height: 1.5;
}

.tbl td:first-child, .tbl td:first-child, .tbl th:first-child, .tbl th:first-child {
