  outline: none !important;
}

.slick-arrow {
  width: 40px;
  height: 40px;
  display: block;
  position: absolute;
  border: none;
  border-radius: 0;
  background: none;
  outline: none;
  box-shadow: none;
  opacity: 1;
  z-index: 6;
  transition-property: background, opacity, visibility;
  transition-duration: 0.2s;
}

