  cursor: default;
}

.slick-arrow.slick-disabled::before, .no-touchevents .slick-arrow.slick-disabled::before {
  opacity: 1;
}

.slick-arrow::before {
  content: none;
}

.slick-arrow::after {
  top: 50%;
  content: "";
  position: absolute;
  opacity: 1;
  pointer-events: none;
  width: 12px;
  height: 12px;
  transform: rotate(45deg);
  border: 0 solid #000;
