  display: block;
  margin-bottom: 1rem;
  text-decoration: none;
  color: currentColor;
}

.product-item__header > a:last-child {
  margin-bottom: 0;
}

.product-item__figure {
  margin-bottom: 4.5rem;
}

.product-item__figure:last-child {
  margin-bottom: 0;
}

.product-item__figure img {
  display: block;
  width: 100%;
  height: 100%;
  max-width: none;
  object-fit: scale-down;
  font-family: "object-fit: scale-down";
}

.product-item__title {
  display: block;
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.6rem;
  line-height: 2.4rem;
  color: #000;
  margin-bottom: 2rem;
}

.product-item__title span {
  transition-property: box-shadow;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.product-item__title:hover {
  color: black;
}

.product-item__title:hover span {
  box-shadow: inset 0 -2px 0 rgba(0, 0, 0, 0.75);
}

.product-item__title:last-child {
  margin-bottom: 0;
}

.product-item__rate {
  display: block;
  width: 90px;
}

.product-item__rate > span {
  display: block;
