  height: 14px;
  background: url(../img/rate.jpg) no-repeat 0 0;
}

.product-item__footer {
  padding: 0 2.4rem;
}

.product-item__meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0;
  min-height: 3.2rem;
}

.product-item__stock {
  display: flex;
  align-items: center;
  flex-basis: 50%;
  font-size: 1.2rem;
  line-height: 1.6rem;
  color: #000;
}

.no-flexwrap .product-item__stock {
  display: inline-block;
  vertical-align: top;
  width: calc(50% - 6px);
}

.product-item__stock > svg {
  display: inline-block;
  vertical-align: top;
  width: 1.8rem;
  height: 1.8rem;
}

.product-item__stock > svg path {
  fill: #6e52dd;
}

.product-item__stock > svg + span {
  padding-left: 0.8rem;
}

.product-item__stock > span {
  padding-top: 0.2rem;
}

.no-flexwrap .product-item__stock > span {
  display: inline-block;
  vertical-align: top;
}

.product-item__stock.off {
  color: #c3c3c3;
}

.product-item__stock.off > svg path {
  fill: currentColor;
}

.product-item__price {
  flex-basis: 50%;
  text-align: right;
  line-height: 1;
}

.no-flexwrap .product-item__price {
  display: inline-block;
  vertical-align: top;
  width: calc(50% - 6px);
}

.product-item__price-new > strong {
  display: inline-block;
  vertical-align: baseline;
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 2.4rem;
  line-height: 3.2rem;
}

.product-item__price-new > em {
  display: inline-block;
  vertical-align: baseline;
  font-style: normal;
  font-size: 1.4rem;
  line-height: 1.8rem;
}

.product-item__option {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 1;
  margin: 0 -1.9rem;
  z-index: 1;
}

.product-item__option::before, .product-item__option::after {
  content: "";
  display: block;
  border: solid #f5f5f5;
  position: absolute;
  z-index: -1;
}

.product-item__option::before {
  width: 100%;
  left: 0;
  top: 0;
  border-width: 2px 0 0;
}

.product-item__option::after {
  width: 0;
  height: 100%;
  left: 50%;
  top: 0;
  border-width: 0 0 0 2px;
  margin-left: -1px;
}

.product-item__option > a {
  flex-grow: 1;
  flex-basis: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.8rem 1rem;
  font-size: 1.2rem;
  line-height: 1.6rem;
  text-decoration: none;
  text-align: center;
  color: #000;
  transition-property: background-color, color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.no-flexwrap .product-item__option > a {
  display: inline-block;
  vertical-align: top;
  width: calc(50% - 6px);
}

.product-item__option > a > svg {
  display: inline-block;
  vertical-align: top;
  width: 2.2rem;
  height: 2.2rem;
}

.product-item__option > a > svg path {
  fill: #6e52dd;
  transition-property: fill;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.product-item__option > a > span {
  padding-top: 0.2rem;
  padding-right: 1rem;
}

.no-flexwrap .product-item__option > a > span {
  display: inline-block;
  vertical-align: top;
}

.product-item__option > a:first-child > svg path {
  fill: #ef4180;
}

.product-item__option > a.added:nth-child(2) > svg {
  background-color: #6e52dd;
  border-radius: 50%;
  box-shadow: 0 0 0 3px #6e52dd;
}

.product-item__option > a.added:nth-child(2) > svg path {
  fill: white;
}

.product-item__option > a:hover {
  background-color: #6e52dd;
  color: white;
}

.product-item__option > a:hover > svg path {
  fill: white;
}

.product-item__sale {
  position: absolute;
  top: 0.5rem;
  right: 1.7rem;
  width: 3.4rem;
  height: 3.4rem;
  padding-left: 0.1em;
  border-radius: 1.7rem;
  background-color: #ef4180;
  font-family: "circe-bold", Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 1.4rem;
  line-height: 3.4rem;
  color: white;
  text-align: center;
  pointer-events: none;
}

.product-item__labels {
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
}

.product-item__label {
  font-size: 1.2rem;
  line-height: 1.6rem;
  color: white;
  text-align: center;
  padding: 1.2rem 2.4rem;
}

.product-item__label_day {
  background-color: #6e52dd;
}

