/* catalog opener */
a.catalog-opener {
  position: relative;
  padding: 1rem 2rem 1rem 5rem;
  font-size: 1.4rem;
  line-height: 2.0rem;
  color: #4a4a4a;
  text-decoration: none;
  border-radius: 2rem;
  margin-left: 0.5rem;
  min-width: 16rem;
  will-change: color;
  transition-property: color;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.touchevents a.catalog-opener {
  user-select: none;
  -webkit-tap-highlight-color: transparent;
}

a.catalog-opener::before, a.catalog-opener::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 14px;
  width: 18px;
  height: 2px;
  background-color: #6e52dd;
  margin-top: -1px;
  will-change: opacity, transform;
  opacity: 0;
  transition-property: transform, opacity;
  transition-duration: 0.2s;
  transition-delay: 0s;
  transition-timing-function: cubic-bezier(0.65, 0.05, 0.36, 1);
}

a.catalog-opener > i {
  position: absolute;
  left: 14px;
  top: 50%;
  width: 18px;
  height: 2px;
  background-color: #6e52dd;
  margin-top: -1px;
  backface-visibility: hidden;
  transition-property: opacity;
  transition-duration: 0.2s;
  transition-delay: 0.1s;
}

a.catalog-opener > i::before, a.catalog-opener > i::after {
  content: '';
  display: block;
  position: absolute;
  width: 18px;
  height: 2px;
  background-color: #6e52dd;
  backface-visibility: hidden;
  transition-property: transform;
  transition-duration: 0.2s;
  transition-delay: 0.1s;
}

a.catalog-opener > i::before {
  top: -5px;
}

a.catalog-opener > i::after {
  top: 5px;
}

a.catalog-opener:hover {
  color: #6e52dd;
}

a.catalog-opener.is-open::before, a.catalog-opener.is-open::after {
  transition-delay: 0.1s;
  opacity: 1;
}

a.catalog-opener.is-open::before {
  transform: rotate(45deg) translateZ(0);
}

a.catalog-opener.is-open::after {
  transform: rotate(-45deg) translateZ(0);
}

a.catalog-opener.is-open > i {
  opacity: 0;
  transition-delay: 0s;
}

a.catalog-opener.is-open > i::before, a.catalog-opener.is-open > i::after {
  transition-delay: 0s;
}

a.catalog-opener.is-open > i::before {
  transform: translate(0, 5px) translateZ(0);
}

a.catalog-opener.is-open > i::after {
  transform: translate(0, -5px) translateZ(0);
}

.search-is-open a.catalog-opener {
  color: white;
}

.search-is-open a.catalog-opener::before, .search-is-open a.catalog-opener::after, .search-is-open a.catalog-opener > i, .search-is-open a.catalog-opener > i::before, .search-is-open a.catalog-opener > i::after {
  background-color: currentColor;
}

.search-is-open a.catalog-opener:hover {
  color: #6e52dd;
  background-color: white;
}

