@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

/* user form */
.user-form {
  margin-bottom: $bottom-space;

  &:last-child {
    margin-bottom: 0; }

  .form-title {
    display: block;
    @include font-alt-bold;
    color: $title-color;
    text-align: center;
    margin-bottom: $bottom-space;

    // font size
    & {
      @include font-h2; } }

  form {
    display: block; }

  .input-wrap {
    &:last-child {
      margin-bottom: 0; } }

  textarea {
    height: 130px;
    max-height: 130px;
    resize: none; }

  .form-buttons {
    float: left;
    margin-right: 30px;

    @include media('<mob-lg') {
      float: none;
      margin: 0; }

    [class*="btn-"] {
      @include media('<mob-lg') {
        display: block;
        width: 100%;
        margin-top: $bottom-space--small;

        &:first-child {
          margin-top: 0; } } } }

  .form-note {
    overflow: hidden;
    min-height: $btn-height;
    @include clear-spacing;
    line-height: 25px;

    &::before {
      content: '';
      display: inline-block;
      vertical-align: middle;
      min-height: inherit; } }

  .form-note__item {
    display: inline-block;
    vertical-align: middle;
    width: calc(100% - 6px);
    @include font($font-size--small, $line-height--small);
    letter-spacing: $letter-spacing; } }

/* user form end */

/* slider */
.swiper-container, .swiper-pagination, .slider-arrows {
  opacity: 0;
  // transition
  $prop: opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed; }

  .is-loaded &, &.is-loaded, .no-js & {
    opacity: 1; } }

.swiper-container, .swiper-pagination-bullet, .slider-arrows {
  pointer-events: none;

  .is-loaded &, &.is-loaded, .no-js & {
    pointer-events: auto; } }

// slider arrows
.slider-arrows {
  height: 0;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 100%;
  //margin-bottom: $bottom-space--article
  pointer-events: none;

  @include media('<desk-sm') {
 }    //margin-bottom: $bottom-space--article--md

  @include media('<mob-lg') {
    //margin-bottom: $bottom-space--article--mob
    display: none; } }

.slider-arrows__holder {
  position: relative; }

.slider-arrow {
  position: absolute;
  top: 50%;
  @include size(nth($slider-button-size, 2), nth($slider-button-size, 1));
  margin-top: -#{round(nth($slider-button-size, 2)/2)};
  color: $base-color;
  will-change: transform;
  //background: rgba(91, 255, 176, 0.51)
  // для тестирования, чтобы видеть контуры кнопки
  //background: none
  z-index: 5;
  cursor: pointer;
  user-select: none;
  -webkit-tap-highlight-color: transparent;

  .slider-arrows & {
    left: auto;
    top: auto;
    bottom: 0;
    pointer-events: auto;
    margin: round((50 - nth($slider-button-size, 1))/2) 0;

    @include media('<desk-sm') {
      margin: round((38 - nth($slider-button-size, 1))/2) 0; }

    @include media('<mob-sm') {
      margin: round((30 - nth($slider-button-size, 1))/2) 0; } }

  svg {
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    width: $slider-arrow-size;
    height: $slider-arrow-size;
    margin: -#{round($slider-arrow-size/2)} 0 0 -#{round($slider-arrow-size/2)};
    // transition
    $prop: opacity;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed; }

    @include media('<desk-sm') {
      width: $slider-arrow-size--mob;
      height: $slider-arrow-size--mob;
      margin: -#{round($slider-arrow-size--mob/2)} 0 0 -#{round($slider-arrow-size--mob/2)}; }


    & + svg {
      opacity: 0; } }

  path {
    fill: currentColor; } }

// prev arrow
.slider-arrow_prev {
  left: 0;

  .slider-arrows & {
    right: nth($slider-button-size, 2); } }

// next arrow
.slider-arrow_next {
  right: 0;

  .slider-arrows & {
    right: 0; } }

// hover, disabled, active
.slider-arrow {

  // hover
  &:not(.swiper-button-disabled):hover {
    svg {
      & + svg {
        opacity: 1; } } }

  // active
  &:not(.swiper-button-disabled):active {
    svg {
      transform: translate(0, 1px); } }

  // disabled
  &.swiper-button-disabled {
    pointer-events: auto;
    cursor: default;
    opacity: 0.33; } }


/* slider pagination */
.swiper-pagination {
  pointer-events: none; }

.swiper-pagination-bullet {
  display: inline-block;
  vertical-align: top;
  $size: 8px;
  @include size($size);
  border-radius: round($size / 2);
  border: none;
  color: $base-color;
  opacity: 0.3;
  margin: 6px;
  pointer-events: auto;
  background: white;
  // transition
  $prop: opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  .swiper-container-horizontal>.swiper-pagination-bullets & {
    margin: 0 6px; }

  // hover
  &:hover {
    opacity: 0.8; } }

.swiper-pagination-bullet-active {
  opacity: 1;
  cursor: default;

  &:hover {
    opacity: 1;
    cursor: default; } }

/* slider end */

/* slick sliders */
// slider
.slick-slide {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important;
  -webkit-focus-ring-color: rgba(255, 255, 255, 0) !important;
  outline: none !important; }

// arrows
.slick-arrow {
  @include size(40px);
  display: block;
  position: absolute;
  border: none;
  border-radius: 0;
  background: none;
  outline: none;
  box-shadow: none;
  opacity: 1;
  z-index: 6;
  transition-property: background, opacity, visibility;
  transition-duration: 0.2s;

  .no-touchevents &:hover {
    opacity: 1;
    background: #e6e6e6; }

  &.slick-disabled, .no-touchevents &.slick-disabled {
    opacity: 0.25;
    -ms-filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
    filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=25);
    cursor: default;

    &::before {
      opacity: 1; } }

  &::before {
    content: none; }

  &::after {
    top: 50%;
    content: "";
    position: absolute;
    opacity: 1;
    pointer-events: none;
    @include size(12px);
    transform: rotate(45deg);
    border: 0 solid #000; } }

.slick-prev {
  left: 0;

  &::after {
    left: 50%;
    border-width: 0 0 2px 2px;
    margin: -6px 0 0 -4px; } }

.slick-next {
  right: 0;

  &::after {
    right: 50%;
    border-width: 2px 2px 0 0;
    margin: -6px -4px 0 0; } }

// dots
.slick-dots {
  margin: 0;
  line-height: 0;

  .#{$user-content} & {
    margin: 0;
    max-width: none; }

  li {
    vertical-align: top;
    width: auto;
    height: auto;
    margin: 0;

    &, .#{$user-content} & {
      padding: 4px 10px;
      margin: 0;

      &::before {
        content: none; } }

    button {
      width: 12px;
      height: 12px;
      padding: 0;
      border-radius: 50%;
      background: $base-color;
      position: relative;
      transition-property: background, border, box-shadow, transform;
      transition-duration: 0.6s;

      &::before {
        content: '';
        position: absolute;
        left: 2px;
        top: 2px;
        height: 8px;
        width: 8px;
        background: #fff;
        border-radius: 50%;
        opacity: 1;
        box-shadow: 0 2px 3px rgba(0, 0, 0, 0.20);
        transition-property: transform, opacity;
        transition-duration: $animation-speed; } }

    &.slick-active button::before {
      opacity: 0; } } }

//.no-touchevents &:hover button::before
/* slick sliders end */

/* images slider */
.images-slider {
  position: relative;
  margin-bottom: $bottom-space;

  @include media('<tab-lg') {
    margin-bottom: $bottom-space--large; } }

.images-slider__list, .slick-dotted.images-slider__list {
  padding-bottom: $bottom-space;
  margin-bottom: 0;
  position: relative;
  z-index: 2; }

.images-slider__item {
  float: none;
  display: inline-block;
  vertical-align: top;
  opacity: 0;
  white-space: normal;
  overflow: hidden;
  position: relative;
  background-color: transparent;

  .slick-initialized & {
    display: block;
    float: left;
    opacity: 1; }

  .no-js & {
    display: block;
    width: 100%;
    opacity: 1;
    margin-bottom: $bottom-space; }

  &::before {
    content: '';
    display: block;
    @include ratio(16, 9);

    .no-js & {
      content: none; } }

  img {
    position: absolute;
    @include position(0);
    text-align: center;
    max-width: none;
    width: 100%;
    height: 100%;
    @include object-fit(contain);

    .no-js &[data-lazy] {
      display: none; }

    .no-js & {
      position: relative;
      @include position(auto);
      width: auto;
      height: auto;
      @include object-fit(fill);
      margin-bottom: 1rem; } }

  .caption {
    display: none;

    .no-js & {
      display: block; } } }

.images-slider {

  .slick-arrow {
    right: 0;
    left: auto;
    top: 100%;
    transform: translate(0, 0); }

  .slick-prev {
    right: 40px; }

  // slider counter
  .slider-counter {
    position: absolute;
    left: 0;
    top: 100%;
    color: $text-color-light;

    .slide-curr {
      color: $text-color; } }

  .slick-dots, .#{$user-content} & .slick-dots {
    position: absolute;
    bottom: $bottom-space + 1;

    li {
      @include padding-horizontal(7px);

      button {
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: white;
        opacity: 0.5;
        transition-property: opacity, transform;
        transition-duration: $animation-speed;

        &::before {
          content: none; } }

      &.slick-active button {
        opacity: 1; }

      .no-touchevents &:hover button {
        opacity: 1;
        transform: scale(2); } } } }

/* images slider end */

/* flashes */
.flashes {
  padding: 0 90px; }

.flashes__item, .images-slider__item .caption {
  @include font(1.4rem, 1.5);
  color: $text-color-light;

  & > *:last-child {
    margin-bottom: 0; } }

/* flashes end */

/* breadcrumbs */
.breadcrumbs {
  color: $text-color-light;
  margin-bottom: $bottom-space--large;

  @include media('<desk-lg') {
    margin-bottom: $bottom-space--middle; }

  &:last-child {
    margin-bottom: 0; } }

.breadcrumbs__list {
  &, .#{$user-content} & {
    padding: 0;
    margin: 0;
    list-style: none;

    li {
      padding: 0 6px 0 0;
      margin: 0;

      &::before, &:first-child::before {
        content: none; } } }

  cursor: default;
  overflow: hidden;
  @include font($font-size--small, $line-height--small);

  .layout-article & {
    max-width: none; }

  li {
    display: inline;
    position: relative;

    &::after {
      display: inline-block;
      vertical-align: baseline;
      content: '•';
      border: none;
      color: $base-color;
      padding-left: 10px; }

    &:last-child::after {
      content: none; } }

  a {
    padding-top: 0.5em;
    color: $text-color;
    text-decoration: none;
    cursor: pointer;

    span {
      border-bottom: 1px solid rgba(white, 0.3); }

    .no-touchevents &:hover {
      color: $base-color;

      span {
        border-color: rgba($base-color, 0.3); } } } }

/* breadcrumbs end */

/* files links */
.file {
  $file-icon-size: 42px;
  @include display-inline-block-top;
  min-height: $file-icon-size;
  padding: 0.5em;
  @include font(16px, 20px);
  color: $text-color;
  text-decoration: none;

  @include clear;

  svg {
    float: left;
    @include size($file-icon-size); }

  & > span, & > em {
    display: block;
    margin-left: #{$file-icon-size};
    padding-left: 1em; }

  & > em {
    display: block;
    font-size: 1.4rem;
    color: $text-color-light; }

  .no-touchevents &:hover {
    background-color: $base-color;

    & > em {
      color: $bg-color; } } }

.st-bg {
  fill: #e2e5e7; }

.st-corner {
  fill: #b0b7bd; }

.st-shadow {
  fill: #cad1d8; }

.st-text {
  fill: #fff; }

/* files links end */

/* feature list */
.feature-list {
  margin-bottom: $bottom-space--medium;

  &:last-child {
    margin-bottom: 0; } }

.feature-list__item {
  margin-bottom: $bottom-space;
  @include clear;

  &:last-child {
    margin-bottom: 0; } }

.feature-list__img {
  float: left;
  width: 25%;
  margin: 0.3em $bottom-space $bottom-space--small 0;

  @include media("<mob-lg") {
    width: auto;
    float: none;
    margin-right: 0; } }

.feature-list__content {
  overflow: hidden;

  h3, p {
    margin-bottom: $bottom-space--small;

    &:last-child {
      margin-bottom: 0; } } }

/* feature list end */

// =============================================

/* main-section */
.main-section {
  position: relative;
  padding-top: 2rem;
  padding-bottom: 2rem; }

.main-section__title {
  @include font-bold;
  font-size: 36px;
  line-height: 42px;
  color: #131313;
  margin-bottom: 2rem;

  &:last-child {
    margin-bottom: 0; } }

.mark-anchor {
  position: absolute;
  left: 0;
  top: -#{$header-height};
  height: 0;
  font-size: 0;
  line-height: 0;
  pointer-events: none;
  overflow: hidden;
  user-select: none; }

/* main-section end */

/* promo slider */
.main-section_promo {
  height: 575px;
  padding-top: 0;
  padding-bottom: 0;
  margin-bottom: -11.5rem;

  &:last-child {
    margin-bottom: 0; } }

.promo-slider {
  height: 100%;
  overflow: hidden;
  position: relative;

  &::before {
    $left: 130px;
    $bottom: 100px;
    content: '';
    display: block;
    width: calc(100% + #{($left + $bottom) * 2});
    height: calc(70px + #{$bottom});
    position: absolute;
    bottom: -#{$bottom};
    left: -#{$left + $bottom};
    background-color: $bg-color;
    //+linear-gradient(180deg, red 0%, blue 100%)
    //clip-path: circle(3000px at 50% -2200px)
    border-radius: 50% 50% 0 0 / 100% 100% 0 0;
    z-index: 9; }

  // arrows
  .slider-arrow {
    display: none;
    top: auto;
    bottom: 140px;
    color: white;
    @include size(nth($slider-promo-button-size, 2), nth($slider-promo-button-size, 1));
    margin-bottom: 60px;

    @include media('<mob-lg') {
      display: none; }

    &::before, &::after {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      background-color: $base-color;
      z-index: -1; } }

  .slider-arrow_prev {
    &::after {
      transform-origin: bottom right; } }

  .slider-arrow_next {
    &::after {
      transform-origin: top left; } }

  .swiper-pagination {
    line-height: 0;
    bottom: 132px; } }

.promo-slider__item {
  //display: flex
  //flex-direction: column
  //text-align: center
  //justify-content: center
  //align-items: center
  position: relative;
  overflow: hidden; }

.promo-slider__item-img {
  position: absolute;
  @include size(100%);
  @include position(0);

  img {
    display: block;
    max-width: 100%;
    margin: 0 auto;
    @include size(100%);
    @include object-fit(scale-down); } }

.promo-slider__item-content {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  flex-basis: 100%;
  width: 100%;
  padding: $header-height 0 11.5rem 37%;
  color: inherit;
  text-decoration: none;
  flex-grow: 1;
  height: 100%; }

.promo-slider__item-title {
  $font-size: 3.6rem;
  $line-height: 4.0rem;
  $font-size--tab: 3.0rem;
  $line-height--tab: 3.6rem;
  $font-size--mob: 1.8rem;
  $line-height--mob: 2.2rem;
  // ===============
  display: block;
  overflow: hidden;
  margin-bottom: 0.6em;
  max-height: round-float($line-height * 3, 1); // maximum 3 line
  @include font($font-size, $line-height);
  letter-spacing: normal;
  width: 43rem;
  max-width: 100%;
  color: white;

  @include media('<tab-lg') {
    font-size: $font-size--tab;
    line-height: $line-height--tab;
    max-height: round-float($line-height * 4, 1); // maximum 4 line
    width: auto; }

  @include media('<mob-lg') {
    font-size: $font-size--mob;
    line-height: $line-height--mob;
    max-height: round-float($line-height * 5, 1); } // maximum 5 line

  &:last-child {
    margin-bottom: 0; } }

.promo-slider__item-footer {

  .btn-def {
    min-width: 20.3rem; } }

/* promo slider end */

/* category-links */
.category-links {
  background-color: white;
  padding: 1.6rem; }

.category-links__title {
  margin-bottom: 2rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  &:last-child {
    margin-bottom: 0; }

  & > svg {
    display: inline-block;
    vertical-align: top;
    width: 3rem;
    height: 3rem;
    margin-right: 1rem;

    path {
      fill: $base-color; } }

  & > strong {
    display: inline-block;
    vertical-align: top;
    @include font-bold;
    font-size: 1.6rem;
    line-height: 2.0rem;
    color: #000;
    margin-right: 1rem;

    a {
      text-decoration: none;
      color: currentColor;

      @include underlinedLinks(#000); } }

  & > em {
    display: inline-block;
    vertical-align: top;
    @include font-light;
    font-style: normal;
    font-size: 1.4rem;
    line-height: 2.0rem;
    color: $base-color; } }

.category-links__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0 0 2rem;
  padding: 0;
  @include clear-spacing;

  &:last-child {
    margin-bottom: 0; }

  li {
    //display: inline-block
    //vertical-align: top
    color: #131313;
    font-size: 1.4rem;
    line-height: 1.8rem;
    letter-spacing: normal;
    padding: 0 0.5rem 1rem 0;

    // for ie 9
    .no-flexwrap & {
      display: inline-block;
      vertical-align: top; } }

  a {
    display: inline-block;
    vertical-align: top;
    color: currentColor;
    text-align: center;
    min-height: 3.2rem;
    border-radius: 1.6rem;
    padding: 0.7rem 0.9rem 0.5rem;
    box-shadow: inset 0 0 0 1px rgba(#bdbdbd, 0.3);
    text-decoration: none;
    // transition
    $prop: border-color, background-color, color;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; }

    &:hover {
      background-color: $base-color;
      border-color: $base-color;
      color: white; } } }


/* category-links end */

/* advise */
$advise-figure-size: 6.4rem 6.4rem;
$advise-figure-space: 2rem;
.advise {
  position: relative;
  padding: 2.0rem 1.6rem 1.6rem;
  background-color: white;
  box-shadow: 0 0 3px rgba(black, 0.17);
  z-index: 1;
  @include clear; }

.advise__figure {
  float: left;
  padding: 0;
  margin-right: $advise-figure-space;
  height: nth($advise-figure-size, 1);
  width: nth($advise-figure-size, 2);
  border-radius: round-float(nth($advise-figure-size, 1) / 2, 1);
  overflow: hidden;
  position: relative;
  border: 3px solid #efefef;

  img {
    display: block;
    border-radius: round-float(nth($advise-figure-size, 1) / 2, 1);
    width: 100%;
    height: 100%;
    max-width: none; } }

.advise__name {
  display: block;
  margin-left: nth($advise-figure-size, 2) + $advise-figure-space;
  @include font-bold;
  font-size: 1.6rem;
  line-height: 2.0rem;
  color: #000;
  margin-bottom: 1rem;

  &:last-child {
    margin-bottom: 0; } }

.advise__past {
  display: block;
  margin-left: nth($advise-figure-size, 2) + $advise-figure-space;
  font-size: 1.4rem;
  line-height: 1.8rem;
  color: #9b9b9b;
  margin-bottom: 1rem;

  &:last-child {
    margin-bottom: 0; } }

.advise-chat {
  display: block;
  margin-left: nth($advise-figure-size, 2) + $advise-figure-space;
  overflow: hidden; }

.advise-chat__list {
  list-style: none;
  padding: 0;
  margin: 0 0 -1rem;
  @include clear;

  li {
    float: left;
    margin: 0 1rem 1rem 0; }

  a {
    $size: 2.4rem;
    display: block;
    height: $size;
    width: $size;
    text-decoration: none;
    text-indent: -9999px;
    overflow: hidden;
    background-repeat: no-repeat;
    background-image: url(../../img/sprite-chat.jpg);

    &.chat-viber {
      background-position: -4px -4px; }

    &.chat-telegram {
      background-position: -38px -4px; }

    &.chat-whatsapp {
      background-position: -77px -4px; } } }

.advise-btn {
  display: block;
  text-decoration: none;
  @include font-bold;
  font-size: 1.4rem;
  line-height: 2.0rem;
  text-align: center;
  background-color: #ffe99c;
  padding: 8px;
  color: #000;
  // transition
  $prop: background-color;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  & > span {
    display: block;
    padding: 1.5rem 2rem;
    background-color: white;
    border-radius: 3px;
    box-shadow: inset 0 0 0 1px #efefef; }

  //hover
  &:hover {
    background-color: $base-color-alt; } }

