  color: currentColor;
  background-color: white;
  border: solid #f5f5f5;
  border-width: 0 0 0 1px;
  // transition
  $prop: background-color;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  // for ie 9
  .no-flexwrap & {
    display: inline-block;
    vertical-align: top;
    width: calc(20% - 6px); }

  // touch
  .touchevents & {
    user-select: none;
    -webkit-tap-highlight-color: transparent; }

  &:first-child {
    border: none; } }

.helper__inner {
  width: calc(100% - 4rem);
  display: flex;
  align-items: center;
  text-align: left;

  & > svg {
    $size: 3.4rem;
    display: inline-block;
    vertical-align: top;
    width: $size;
    min-width: $size;
    height: $size;
    opacity: 0.8;

    path {
      fill: $base-color;
      // transition
      $prop: fill;
      transition: {
        property: $prop;
        duration: $animation-speed;
        timing-function: ease-in-out; } }

    & + span {
      margin-left: 1.5rem; } }

  & > span {
    font-size: 1.4rem;
    line-height: 1.8rem;
    color: rgba(black, 0.8);
    // transition
    $prop: color;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; } } }


.helper__item {
  // hover
  &:hover {
    background-color: $base-color;
    color: white;

    .helper__inner {
      & > svg path {
        fill: white; }

      & > span {
        color: white; } } } }

/* helper */
