@import "../utils/variables";
@import "../utils/mixins";
@import "../../libs/include-media/dist/include-media";

html {
  font-size: 62.5%;

  // блокировка скролла
  &.css-scroll-fixed:not(.open-only-mob) {
    @include scroll-blocked; }

  &.css-scroll-fixed.open-only-mob {
    @include media('<tab-lg') {
      @include scroll-blocked; } } }

/* === LAYOUT === */
.max-wrap {
  max-width: $page-layout-width;
  @include push-auto;

  //&.layout
 }  //max-width: #{$page-layout-width + nth($page-layout-space, 2) * 2}

.layout {
  @if nth($page-layout-space, 2) != 0 {
    @include padding-horizontal(nth($page-layout-space, 2)); } }

.layout-narrow {
  @if nth($page-layout-space-narrow, 2) != 0 {
    @include padding-horizontal(nth($page-layout-space-narrow, 2));

    @include media('<desk-md') {
      @include padding-horizontal(nth($page-layout-space-narrow--tab, 2)); } } }

// выравнивание текстовых элементов на странице статьи (контент обернут в .layout-article)
// и их максимальная ширина
.layout-article {
  padding: $text-layout-space;

  @include media('<tab-lg') {
    padding: $text-layout-space--md; }

  @include media('<tab-sm') {
    padding: $text-layout-space--sm; }

  & > p, & > ul, & > ol, & > h1, & > h2, & > h3, & > h4, & > h5, & > h6, & > hr, & > blockquote, & > .layout-text, .imgc figcaption {
    max-width: $text-layout-width;
    @include padding-horizontal(0);
    @include push-auto; }

  .layout-flood {
    max-width: $flood-layout-width;
    @include padding-horizontal(0);
    @include push-auto; }

  // элеметы без отступов по бокам
  // минусовые маржины по горизонтале имеют значение только внутри .layout-article
  .layout-full-width, .layout-wide {

    @include margin-horizontal(-#{nth($text-layout-space, 2)});

    @include media('<tab-lg') {
      @include margin-horizontal(-#{nth($text-layout-space--md, 2)}); }

    @include media('<tab-sm') {
      @include margin-horizontal(-#{nth($text-layout-space--sm, 2)}); } } }

// максимальная ширина текстовых элементов на внутренней (текстовой) странице
.max-wrap-text {
  //max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2}
  max-width: $text-layout-width; }

//+media('<tab-lg')
//max-width: #{$text-layout-width + (nth($text-layout-space--md, 2))*2}

//+media('<tab-sm')
//max-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2}

// выравнивание текстовых элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-text {
  max-width: #{$text-layout-width + (nth($text-layout-space, 2))*2};
  @include padding-horizontal(nth($text-layout-space, 2));
  @include push-auto;

  @include media('<tab-lg') {
    max-width: #{$text-layout-width + (nth($text-layout-space--md, 2))*2};
    @include padding-horizontal(nth($text-layout-space--md, 2)); }

  @include media('<tab-sm') {
    max-width: #{$text-layout-width + (nth($text-layout-space--sm, 2))*2};
    @include padding-horizontal(nth($text-layout-space--sm, 2)); } }

// максимальная ширина альтернативных элементов на внутренней (текстовой) странице
.max-wrap-flood {
  //max-width: #{$flood-layout-width + (nth($text-layout-space, 2))*2}
  max-width: $flood-layout-width; }

//+media('<tab-lg')
//max-width: #{$flood-layout-width + (nth($text-layout-space--md, 2))*2}

//+media('<tab-sm')
//max-width: #{$flood-layout-width + (nth($text-layout-space--mob, 2))*2}

// выравнивание альтернативных элементов на внутренней (текстовой) странице
// и их максимальная ширина
.layout-flood {
  max-width: #{$flood-layout-width + (nth($text-layout-space, 2))*2};
  @include padding-horizontal((nth($text-layout-space, 2)));
  @include push-auto;

  @include media('<tab-lg') {
    max-width: #{$flood-layout-width + (nth($text-layout-space--md, 2))*2};
    @include padding-horizontal((nth($text-layout-space--md, 2))); }

  @include media('<tab-sm') {
    max-width: #{$flood-layout-width + (nth($text-layout-space--sm, 2))*2};
    @include padding-horizontal((nth($text-layout-space--sm, 2))); }

  p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
    max-width: none; } }

// элеметы с отступами по бокам
.layout-wide {
  @include padding-horizontal((nth($text-layout-space, 2)));

  @include media('<tab-lg') {
    @include padding-horizontal((nth($text-layout-space--md, 2))); }

  @include media('<tab-sm') {
    @include padding-horizontal((nth($text-layout-space--sm, 2))); }

  p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
    max-width: none; } }

// элеметы без отступов по бокам
.layout-full-width {

  p, ul, ol, h1, h2, h3, h4, h5, h6, hr {
    max-width: none; } }

// выравнивание по центру блочных элементов
.push-auto {
  margin-left: auto;
  margin-right: auto; }

// лэяут страницы
.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: stretch;
  margin: 0 auto;
  //min-height: 100vh
  //min-height: 100% // not support ie
  //height: 100%
  //overflow: hidden
  //height: 100vh
  min-height: 100vh;
  position: relative; }

/* === LAYOUT end === */

/* === HEADER === */
.header {
  position: fixed;
  width: 100%;
  min-width: $page-layout-min;
  height: $header-height;
  z-index: 10;
  // transition
  $prop: box-shadow;
  transition: {
  	property: $prop;
  	duration: $animation-speed;
  	timing-function: ease-in-out; }

  .page-scrolled & {
    box-shadow: 0 2px 5px rgba(black, 0.15); }

  //+media('<tab-lg')
 }  //height: $header-height--sm

.header-bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: $base-color;
  opacity: 0;
  // transition
  $prop: background-color, opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  .page-scrolled & {
    opacity: 0.85; } }

/* header-top */
.header-top {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 9; }

.header-top__l {
  width: calc(100% - 4rem - #{$header-top__r-width});
  max-width: 50%;

  // for ie 9
  .no-flexwrap & {
    display: inline-block;
    vertical-align: top; } }

.header-top__r {
  width: $header-top__r-width;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;

  // for ie 9
  .no-flexwrap & {
    display: inline-block;
    vertical-align: top; }

  & > div {
    // for ie 9
    .no-flexwrap & {
      display: inline-block;
      vertical-align: top; } } }

/* add-menu */
.add-menu {
  position: relative;
  float: left;
  font-size: 1.5rem;
  line-height: 1.4;

  //&.loaded
  //display: flex

  html:not(.no-js) &:not(.loaded) {
    opacity: 0;
    visibility: hidden;
    user-select: none;
    // transition
    $prop: opacity;
    overflow: hidden;
    will-change: $prop;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; } } }

.add-menu__list {
  display: inline-block;
  vertical-align: top;
  list-style: none;
  margin: 0;
  padding: 0;

  li {
    display: inline-block;
    vertical-align: top;
    margin-right: 2rem; } }

// okaynav
.okayNav__nav--visible {
  overflow: hidden;
  white-space: nowrap;

  li {
    float: left; } }

.okayNav__nav--invisible {
  position: absolute;
  top: 100%;
  left: -0.5rem;
  max-height: 10rem;
  width: calc(100% + 1rem);
  overflow-x: hidden;
  overflow-y: auto;
  list-style: none;
  margin: 0;
  padding: 0.5rem 0 0;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  $translate: 0, -2px;
  transform: translate($translate) !important;
  transform: translate3d($translate, 0) !important;
  //background-color: $base-color
  background-color: $bg-color;
  box-shadow: 0 2px 5px rgba(black, 0.15);
  border-radius: 2px;
  // transition
  $prop: transform, opacity, visibility;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  &.nav-open {
    opacity: 1;
    visibility: visible;
    $translate: 0, 0;
    transform: translate($translate) !important;
    transform: translate3d($translate, 0) !important;
    pointer-events: auto; }

  li {
    display: inline-block;
    vertical-align: top;
    margin: 0 1rem 0.5rem; }

  a {
    color: $text-color;
    text-decoration: none;
    // transition
    $prop: color;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; }

    @include underlinedLinks($text-color); } }

.okayNav__menu-toggle {
  position: relative;
  display: inline-block;
  vertical-align: top;
  text-decoration: none;
  padding-right: 1.2rem;

  span {
    user-select: none;
    pointer-events: none; }

  i {
    position: absolute;
    top: 50%;
    right: 0.1rem;
    margin-top: -0.5rem;
    display: inline-block;
    width: 0.5rem;
    height: 0.5rem;
    border: solid currentColor;
    border-width: 0 1px 1px 0;
    transform: rotate(0.125turn);
    user-select: none;
    pointer-events: none; }

  &:active {
    top: 1px; }

  &.icon--active {
    i {
      border-width: 1px 0 0 1px;
      margin-top: -0.1rem; } } }

.add-menu__list a,
.okayNav__menu-toggle {
  color: rgba(white, 0.5);
  text-decoration: none;
  // transition
  $prop: color;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  @include underlinedLinks(white); }

/* add-menu end */

/* header contacts */
.cont {
  position: relative;
  padding-top: 5px;
  min-width: 14rem;

  .header-top & {
    margin-right: 3rem; } }

.chats {
  width: 100%;
  list-style: none;
  margin: 0;
  padding: 0;
  @include font-light;
  font-size: 1.2rem;
  line-height: 1.6rem;
  color: white;
  @include clear;

  li {
    float: left;
    margin: 0 0.5rem 0.2rem 0; }

  a {
    color: currentColor;
    text-decoration: none;

    @include underlinedLinks(white); } }

.phones {
  font-size: 1.6rem;
  line-height: 2.0rem;
  color: white;

  // opener
  & > a {
    text-decoration: none;
    color: currentColor;
    position: relative;
    display: inline-block;
    vertical-align: top;
    padding-right: 1.2rem;
    user-select: none;
    -webkit-tap-highlight-color: transparent;

    @include underlinedLinks(white);

    &:active {
      top: 1px; }

    strong, b {
      @include font-extra-bold; }

    i {
      position: absolute;
      top: 50%;
      right: 0.1rem;
      margin-top: -0.5rem;
      display: inline-block;
      width: 0.5rem;
      height: 0.5rem;
      border: solid currentColor;
      border-width: 0 1px 1px 0;
      transform: rotate(0.125turn);
      user-select: none;
      pointer-events: none; } }

  &.is-open > a i {
    border-width: 1px 0 0 1px;
    margin-top: -0.1rem; } }

.phones-drop {
  position: absolute;
  left: -0.5rem;
  top: 100%;
  width: 9.5em;
  margin: 0;
  padding: 0.5rem;
  list-style: none;
  color: $text-color;
  opacity: 0;
  visibility: hidden;
  $translate: 0, -2px;
  transform: translate($translate);
  transform: translate3d($translate, 0);
  //background-color: $base-color
  background-color: $bg-color;
  box-shadow: 0 2px 5px rgba(black, 0.15);
  border-radius: 2px;
  // transition
  $prop: transform, opacity, visibility;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  .is-open > & {
    opacity: 1;
    visibility: visible;
    $translate: 0, 0;
    transform: translate($translate);
    transform: translate3d($translate, 0); }

  li {
    margin-bottom: 0.5rem;

    &:last-child {
      margin-bottom: 0; } }

  a {
    text-decoration: none;
    color: $text-color;

    @include underlinedLinks($text-color);

    strong, b {
      @include font-extra-bold; } } }

/* header contacts end */

/* cart-link */
.cart-link {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  text-decoration: none;
  color: white;
  padding: 0.6rem 0.8rem 0.7rem;
  border-radius: 0 0 0.7rem 0.7rem;
  // transition
  $prop: background-color, color;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  .header-top & {
    margin-left: 2.8rem; }

  // for ie 9
  .no-flexwrap & {
    display: inline-block;
    vertical-align: top; }

  // tooltip
  &[data-title]::before {
    content: attr(data-title);
    display: block;
    min-width: 100%;
    padding: 0.4rem 0.8rem;
    position: absolute;
    right: 0;
    top: 100%;
    margin-top: 2px;
    @include font(1.2rem, 2rem);
    text-align: center;
    letter-spacing: normal;
    white-space: nowrap;
    z-index: 2;
    //color: white
    color: $text-color;
    @include antialiased;
    opacity: 0;
    visibility: hidden;
    $translate: 0, -2px;
    transform: translate($translate);
    transform: translate3d($translate, 0);
    //background-color: $base-color
    background-color: $bg-color;
    box-shadow: 0 2px 5px rgba(black, 0.15);
    border-radius: 2px;
    // transition
    $prop: transform, opacity, visibility;
    will-change: $prop;
    transition: {
      property: $prop;
      delay: 0s;
      duration: $animation-speed;
      timing-function: ease-in-out; } }

  svg {
    display: inline-block;
    vertical-align: top;

    background-color: white;
    width: 3rem;
    height: 3rem;
    border-radius: 1.5rem;

    // transition
    $prop: background-color;
    transition: {
      property: $prop;
      duration: $animation-speed;
      timing-function: ease-in-out; }

    path {
      fill: $base-color;

      // transition
      $prop: fill;
      transition: {
        property: $prop;
        duration: $animation-speed;
        timing-function: ease-in-out; } }

    & + .cart-link__text {
      padding-left: 1.2rem; } }

  // counter
  .counter {
    display: block;
    position: absolute;
    left: 0.5rem;
    top: 0.5rem;
    background-color: #e61f1f;
    $size: 1.2rem;
    width: $size;
    height: $size;
    padding: 0.1rem 0 0 0;
    font-style: normal;
    font-size: 0.9rem;
    line-height: $size;
    text-align: center;
    color: white;
    border-radius: round-float($size / 2, 1);
    z-index: 10; } }

.cart-link__text {
  font-size: 1.2rem;
  line-height: 1;
  color: currentColor;

  & > strong, & > span {
    @include font-normal;
    display: block; } }

// hover
.cart-link {
  &:hover {
    color: $base-color;
    //background-color: white
    background-color: $bg-color;

    svg {
      background-color: $base-color;

      path {
        fill: white; } } }

  // tooltip
  .no-touchevents &[data-title]:hover {
    &::before {
      transition-delay: 0.6s;
      opacity: 1;
      visibility: visible;
      $translate: 0, 0;
      transform: translate($translate);
      transform: translate3d($translate, 0); } } }

/* cart-link end */

/* header-common */
.header-common {
  display: flex;
  align-items: flex-start;
  position: relative;
  padding-top: 2.3rem;
  padding-bottom: 1rem;
  z-index: 5;
  @include clear; }

/* header-common end */

/* logo */
.logo-wrap {
  margin-top: -1.2rem;

  // for ie 9
  .no-flexwrap & {
    display: inline-block;
    vertical-align: top; } }

.logo {
  position: relative;
  float: left;
  background: url(../../img/logo.png) no-repeat;
  width: 165px;
  height: 63px;
  text-indent: -9999px;
  overflow: hidden;
  margin: 0 2rem 0 0;
  z-index: 5;

  a, & > span {
    display: block;
    height: 100%; } }

/* logo end */

/* site-nav */
.site-nav {
  position: relative;
  z-index: 1;
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  $height: 50px;
  height: $height;

  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    z-index: -1;
    // transition
    //$prop: background-color
    //will-change: $prop
    //transition:
    //property: $prop
    //duration: $animation-speed
 }    //timing-function: ease-in-out

  &.search-is-open {
    &::before {
      background-color: $base-color;
      box-shadow: 0 0 2px rgba(black, 0.15); } }

  &, &::before {
    border-radius: round($height / 2); }

  // for ie 9
  .no-flexwrap & {
    display: inline-block;
    vertical-align: top; }

  // for ie 9
  .no-flexwrap & > div {
    display: inline-block;
    vertical-align: top; } }

.site-nav__grid {
  position: relative;
  display: flex;
  align-items: center;

  // for ie 9
  .no-flexwrap & > div {
    display: inline-block;
    vertical-align: top; } }

/* site-nav end */

/* catalog opener */
a.catalog-opener {
  $btn-menu-size: $header-height--sm;
  $btn-menu-lines-width: 18px;
  $btn-menu-lines-height: 2px;
  $btn-menu-lines-space: 3px;
  $btn-menu-lines-color: $base-color;
  $btn-menu-lines-color--mob: $base-color;
  $btn-menu-animation-speed: $animation-speed;
  $btn-menu-animation-delay: round-float($animation-speed*0.5, 2);

  $user-options-cross-width: $btn-menu-lines-width;
  $user-options-cross-height: $btn-menu-lines-height;
  $user-options-cross-color: $btn-menu-lines-color;
  $user-options-cross-color--mob: $btn-menu-lines-color--mob;

  // =====================

  //display: none
  position: relative;
  padding: 1rem 2rem 1rem 5rem;
  font-size: 1.4rem;
  line-height: 2.0rem;
  color: #4a4a4a;
  text-decoration: none;
  border-radius: 2rem;
  margin-left: 0.5rem;
  min-width: 16rem;
  // transition
  $prop: color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  //&:active
  //transform: translate(0, 1px)

  // убрать подсветку при клике на тачскринах
  .touchevents & {
    user-select: none;
    -webkit-tap-highlight-color: transparent; }

  // крест
  &::before, &::after {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: 14px;
    @include size($user-options-cross-width, $user-options-cross-height);
    background-color: $user-options-cross-color;
    margin-top: -#{round($user-options-cross-height / 2)};
    will-change: opacity, transform;
    opacity: 0;
    transition: {
      property: transform, opacity;
      duration: $btn-menu-animation-speed;
      delay: 0s;
      timing-function: cubic-bezier(0.65, 0.05, 0.36, 1); } }

  // бутерброд
  & > i {
    position: absolute;
    left: 14px;
    top: 50%;
    @include size($btn-menu-lines-width, $btn-menu-lines-height);
    background-color: $btn-menu-lines-color;
    margin-top: -#{round($btn-menu-lines-height / 2)};
    backface-visibility: hidden;
    transition: {
      property: opacity;
      duration: $btn-menu-animation-speed;
      delay: $btn-menu-animation-delay; }

    &::before, &::after {
      content: '';
      display: block;
      position: absolute;
      @include size($btn-menu-lines-width, $btn-menu-lines-height);
      background-color: $btn-menu-lines-color;
      backface-visibility: hidden;
      transition: {
        property: transform;
        duration: $btn-menu-animation-speed;
        delay: $btn-menu-animation-delay; } }

    //first line menu
    &::before {
      top: -#{$btn-menu-lines-height + $btn-menu-lines-space}; }

    //third line menu
    &::after {
      top: #{$btn-menu-lines-height + $btn-menu-lines-space}; } }

  // hover
  &:hover {
    color: $base-color; }

  // opened
  &.is-open {

    &::before, &::after {
      transition-delay: $btn-menu-animation-delay;
      opacity: 1; }

    &::before {
      transform: rotate(45deg) translateZ(0); }

    &::after {
      transform: rotate(-45deg) translateZ(0); }

    & > i {
      opacity: 0;
      transition-delay: 0s;

      &::before, &::after {
        transition-delay: 0s; }

      &::before {
        transform: translate(0, #{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); }

      &::after {
        transform: translate(0, -#{$btn-menu-lines-height + $btn-menu-lines-space}) translateZ(0); } } }

  .search-is-open & {
    color: white;

    &::before, &::after, & > i, & > i::before, & > i::after {
      background-color: currentColor; }

    &:hover {
      color: $base-color;
      background-color: white; } } }

/* catalog opener end */

/* nav */
.nav-wrap {
  // transition
  $prop: transform, opacity, visibility;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  .search-is-open & {
    opacity: 0;
    visibility: hidden;
    $scale: 1, 1;
    transform: scale3d($scale, 1);
    transform: scale($scale); } }

.nav {
  position: relative; }

.nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  line-height: 1;
  @include clear;

  li {
    float: left;
    margin: 0.25rem 2rem 0.25rem 0;

    @include media('<desk-md') {
      margin: 0.25rem 1rem 0.25rem 0; } }

  a {
    @include font-bold;
    color: #232323;
    font-size: 1.4rem;
    text-decoration: none;

    @include underlinedLinks(#232323); } }


/* nav end */

/* search panel */
$search-form-size: 4rem 100%;
$search-btn-size: 4rem 4rem;
$search-btn-space: 0 2rem;
.search-form {
  position: absolute;
  left: 0;
  top: 50%;
  height: nth($search-form-size, 1);
  width: nth($search-form-size, 2);
  margin-top: -#{round-float(nth($search-form-size, 1) / 2, 1)};
  z-index: 99;
  opacity: 0;
  visibility: hidden;
  //$scale: 1, 1
  //transform: scale3d($scale, 1)
  //transform: scale($scale)
  // transition
  //$prop: transform, opacity, visibility
  //will-change: $prop
  //transition:
  //property: $prop
  //duration: $animation-speed
  //timing-function: ease-in-out

  .search-is-open & {
    opacity: 1;
    visibility: visible;
    //$scale: 1, 1
    //transform: scale3d($scale, 1)
 }    //transform: scale($scale)

  form {
    display: block;
    margin: 0;
    padding: 0;
    height: 100%; } }

.search-form__field {
  position: relative;
  margin: 0;
  z-index: auto;
  padding-right: nth($search-btn-space, 2) * 2 + nth($search-btn-size, 2);

  .search-form__input {
    $font-size: 1.6rem;
    $line-height: 2.0rem;
    display: block;
    padding: #{round-float((nth($search-form-size, 1) - $line-height) / 2, 1)} 1.5rem;
    width: 100%;
    height: nth($search-form-size, 1);
    color: #000;
    cursor: text;
    background-color: white;
    //box-shadow: inset 1px 1px 2px rgba(black, 0.6)
    box-shadow: none;
    border: 0;
    border-radius: 0.5rem;
    font-family: inherit;
    font-size: $font-size;
    line-height: $line-height; } }

// search panel buttons
.search-form__btn, .toggle-search {
  height: nth($search-btn-size, 1);
  width: nth($search-btn-size, 2);
  border-radius: round-float(nth($search-btn-size, 1) / 2, 1);
  margin: 0 nth($search-btn-space, 2);
  // transition
  $prop: background-color, color;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  & > svg {
    $size: 20px;
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    width: $size;
    height: $size;
    margin: -#{round-float($size / 2, 1)} 0 0 -#{round-float($size / 2, 1)};

    path {
      fill: currentColor; } }

  & > span {
    @include hide-text; } }

// search panel submit button
.search-form__btn {
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -#{round-float(nth($search-btn-size, 1) / 2, 1)};
  display: inline-block;
  vertical-align: top;
  background-color: $base-color;
  color: white;

  & > span {
    @include hide-text; }

  & > input {
    position: absolute;
    width: 100%;
    height: 100%;
    border: none;
    background: none;
    box-shadow: none;
    padding: 0;
    margin: 0; }

  & > svg {
    $size: 20px;
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    width: $size;
    height: $size;
    margin: -#{round-float($size / 2, 1)} 0 0 -#{round-float($size / 2, 1)};

    path {
      fill: currentColor; } }

  // hover
  &:hover {
    background-color: white;
    color: $base-color; } }

// search panel switcher button
.toggle-search {
  position: relative;
  display: block;
  background-color: white;
  color: $base-color;

  .search-is-open & {
    opacity: 0;
    visibility: hidden; }

  // active
  &:active {
    top: 1px;
    box-shadow: inset 0 2px 2px rgba(black, 0.3); }

  // hover
  &:hover {
    background-color: $base-color;
    color: white; } }

/* search panel end */
