  line-height: 1.8rem;
  color: #e0e0e0;
  text-decoration: none;
  border: 1px solid #494949;
  border-radius: 8px;
  // transition
  $prop: border-color;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  &:last-child {
    margin-bottom: 0; }

  svg {
    $size: 18px;
    display: block;
    position: absolute;
    top: 50%;
    left: 1.3rem;
    height: $size;
    width: $size;
    margin-top: -#{round($size / 2)};

    path, circle {
      fill: #888;
      // transition
      $prop: fill;
      will-change: $prop;
      transition: {
        property: $prop;
        duration: $animation-speed;
        timing-function: ease-in-out; } } }

  // hover
