/* footer bottom end */

/* license */
.license {
  color: rgba(#848484, 0.6); }

/* license end */

/* developer */
.developer {
  display: inline-block;

  a {
    text-decoration: none;

    .no-touchevents &:hover {
      text-decoration: none; } } }

.developer__label {
  @include media('<mob-lg') {
    display: block; }

  i {
    @include media('<mob-lg') {
      display: none; } } }

.developer__logo {
  @include display-inline-block-top;
  position: relative;
  top: 5px;
  padding-right: 3px;
  @include size(154px, 20px);
  text-align: left;
  text-indent: -9999px;
  opacity: 0.3;
  // transition
  $prop: opacity;
  will-change: $prop;
  transition: {
    property: $prop;
    duration: $animation-speed;
    timing-function: ease-in-out; }

  &::before, &::after {
    @include pseudo();
    @include position(0, 0);
    @include size(149px, 16px); }

  &::before {
    @include pseudo();
    @include position(4px, 4px);
    background: url('data:image/png;base64,');
    background-size: 2px; }

  &::after {
    @include pseudo();
    background: #00ef85 url('data:image/png;base64,') no-repeat center center;
    background-size: auto 16px; }

  // hover
  &:hover {
    opacity: 1; } }

/* developer end */
